.settingsWrapper{display:flex;flex-direction:column;justify-content:center;align-items:center;padding-top:20px;padding-bottom:20px}.settingsWrapper .userSettings{display:flex}@media(max-width: 580px){.settingsWrapper .userSettings{flex-direction:column}}.settingsWrapper .avatar{width:200px;height:200px;background-color:#999;border-radius:500px;background-position:0px 80%;background-size:100%;display:block;position:relative;cursor:pointer;margin-right:30px}.settingsWrapper .avatar::before{content:"";transition:background-color .2s}.settingsWrapper .avatar:hover::before{content:"Нажмите чтоб сменить";position:absolute;width:100%;height:100%;background-color:rgba(0,0,0,.6);display:flex;border-radius:500px;justify-content:center;align-items:center;color:#fff}.settingsWrapper input[type=file]{display:none}.settingsWrapper .contentBlock{width:calc(100% - 40px);padding:20px;border-radius:12px;background-color:rgba(255,255,255,.8);backdrop-filter:blur(80px);margin-bottom:20px}.settingsWrapper .contentBlock h1{margin:0;color:#333;font-size:28px}.settingsWrapper .contentBlock p{margin-bottom:8px}.settingsWrapper .contentBlock input{padding-left:15px;height:50px;border-radius:12px;border:1px solid #d8d8d8;font-size:15px}@media(max-width: 580px){.settingsWrapper .contentBlock input{width:calc(100% - 15px)}}.settingsWrapper .contentBlock .row{display:flex;flex-wrap:wrap}.settingsWrapper .contentBlock .row div{margin-right:15px}@media(max-width: 580px){.settingsWrapper .contentBlock .row div{width:100%;margin-right:0}}.settingsWrapper .contentBlock .bgPreview{height:128px;width:256px;border:1px solid #d8d8d8;border-radius:12px;background-size:cover !important;background-position:center !important}.settingsWrapper .contentBlock p.titleBG{text-align:center}.settingsWrapper .contentBlock .backgrounds{display:flex;justify-content:space-between;flex-wrap:wrap}.settingsWrapper .contentBlock .backgrounds>div{cursor:pointer;padding-bottom:10px}.settingsWrapper .contentBlock .backgrounds>div.selected{background:rgba(0,0,0,.07);border-radius:12px}.settingsWrapper button{background-color:#4978ff;padding:10px 20px;border-radius:12px;border:none;color:#fff;margin-top:15px;cursor:pointer}.settingsWrapper button.bgRed{width:100%;display:block;background-color:#ff5656}